.pagination {
  align-items: center;
  border: 2px solid var(--primary-color);
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  height: 120px;
  justify-content: center;
  line-height: 1;
  margin: 60px auto 30px;
  outline: none;
  width: 120px;
}

.load-more {
  color: var(--dark-gray-color);
  font-size: 15px;
  font-weight: 700;
  margin: 10px 0 6px;
}

.page-number {
  color: var(--secondary-text-color);
  font-size: 12px;
  font-weight: 500;
}
